        background: linear-gradient(left, rgba(224, 224, 224, 0.5), rgba(224, 224, 224, 0))
    }
    .toggle-sidebar:after {
        position: absolute;
        right: -11px;
        top: 0;
        width: 20px;
        font-size: 1.2em;
        line-height: 1.1em;
        padding-bottom: .15em;
        -moz-border-radius-bottomright: 0.3em;
