        text-align: center;
        background: #f2f2f2;
        border-bottom: 1px solid #e0e0e0;
        border-right: 1px solid #e0e0e0;
        content: "\00BB";
        text-indent: -1px
    }
    .collapse-sidebar .toggle-sidebar {
        text-indent: 0px;
        right: -20px;
        width: 19px
    }
    .collapse-sidebar .toggle-sidebar:hover {
        background: #e9e9e9
    }
    .collapse-sidebar .toggle-sidebar:after {
        border-left: 1px solid #e0e0e0;
